.page-item{
    width: 40px;
    height: 40px;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 17px;
    font-weight: 600;
  }
  
  .pagination{
      width: 50%;
      margin: auto;
      display: flex;
      gap:10px;
      justify-content: center;
      text-decoration: none;
      list-style-type: none;
   }
   .active{
     color:#4ea819
   }
   .page-item:hover{
      color:green
   }